#slide {
  -webkit-animation: slide-top 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 8.5s
    both;
  animation: slide-top 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 8.5s both;
}

header .banner-text #construction {
  letter-spacing: 5px;
}

@-webkit-keyframes slide-top {
  0% {
    -webkit-transform: translateY(-100px);
    transform: translateY(-100px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 100%;
  }
}
@keyframes slide-top {
  0% {
    -webkit-transform: translateY(-100px);
    transform: translateY(-100px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 100%;
  }
}

/* header social links */
header .social {
  margin: 0;
  padding: 0;
  font-size: 30px;
  text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.8);
}
header .social li {
  display: inline-block;
  margin: 0 15px;
  padding: 0;
}

header .social li i {
  color: #fff;
  -webkit-animation-name: llor;
          animation-name: llor;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  transition: all;
}
header .social li i:hover {
  color: rgb(11, 72, 88);
  -webkit-animation: roll 1s;
          animation: roll 1s;
  transition: all;
  font-size: 45px;
}

@-webkit-keyframes roll {
  0% {
    color: #fff;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    font-size: 30px;
  }
  100% {
    color: rgb(11, 72, 88);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    font-size: 45px;
  }
}

@keyframes roll {
  0% {
    color: #fff;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    font-size: 30px;
  }
  100% {
    color: rgb(11, 72, 88);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    font-size: 45px;
  }
}
@-webkit-keyframes llor {
  0% {
    color: rgb(11, 72, 88);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    font-size: 45px;
  }
  100% {
    color: #fff;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    font-size: 30px;
  }
}
@keyframes llor {
  0% {
    color: rgb(11, 72, 88);
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    font-size: 45px;
  }
  100% {
    color: #fff;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    font-size: 30px;
  }
}

.Typist {
  display: inline;
}

.Typist .Cursor {
  display: inline-block;
  color: black;
}

.Cursor--blinking {
  opacity: 1;
  -webkit-animation: blink 1s linear infinite;
          animation: blink 1s linear infinite;
}

@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

