.hex-wrap {
  text-align: center;
  position: relative;
  display: inline-block;
  width: 100px;
  height: 100px;
  margin: 10px 0;
}

.hexagon1 {
  /* background-color: #04c2c9; */
  background-color: rgb(214, 135, 43);
  height: 100%;
  width: calc(100% * 0.57735);
  display: inline-block;
}

.hexagon1:before {
  position: absolute;
  top: 0;
  right: calc((100% / 2) - ((100% * 0.57735) / 2));
  background-color: inherit;
  height: inherit;
  width: inherit;
  content: '';
  -webkit-transform: rotateZ(60deg);
          transform: rotateZ(60deg);
}

.hexagon1:after {
  position: absolute;
  top: 0;
  right: calc((100% / 2) - ((100% * 0.57735) / 2));
  background-color: inherit;
  height: inherit;
  width: inherit;
  content: '';
  -webkit-transform: rotateZ(-60deg);
          transform: rotateZ(-60deg);
}

